}
.menuBtn{
    display: none;
}
#ProfilePicture{
    position: relative;
    display: inline-block;
    background-image: url(/www.stephencases.com/static/media/Headshot_2018.01e3a48c.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    top: 20px;
    width: 250px;
    height: 240px;
    box-shadow: inset 0px 0px 10px rgba(138, 169, 205, 0.5);
