.login-btn{
    position: absolute;
    display: none;
    width: 100%;
    height: 28px;
    bottom: 0;
    padding-top: 2px;
    background-color: rgba(144, 174, 222, 0.5);
    box-shadow: 0px -1px 10px rgba(144, 174, 222, 0.4);
    font-size: 18px;
    font-weight: bold;
    color: white;
    text-shadow: 0px 0px 3px rgb(86, 86, 86);
    cursor: pointer;
        
    -webkit-animation-name: login-slideUp;
        
            animation-name: login-slideUp;
    -webkit-animation-duration: .6s;
            animation-duration: .6s;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out;
}
    @-webkit-keyframes login-slideUp {
    0%  {
        bottom: -40px;
    }
    100% {
        bottom: 0;
    }
  }
    @keyframes login-slideUp {
    0%  {
        bottom: -40px;
    }
    100% {
        bottom: 0;
    }
  }
.login-btn:hover{
    text-shadow: 0px 0px 3px rgb(86, 86, 86);
    padding-bottom: 8px;
    bottom: 0;
    background-color: rgba(113, 155, 224, 0.8);

    -webkit-animation-name: nudgeUp;

            animation-name: nudgeUp;
    -webkit-animation-duration: .1s;
            animation-duration: .1s;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out;
}
    @-webkit-keyframes nudgeUp {
        0%  {
            bottom: -3px;
            background-color: rgba(144, 174, 222, 0.5);
        }
        100% {
            bottom: 0px;
            background-color: rgba(113, 155, 224, 0.8);
        }
    }
    @keyframes nudgeUp {
        0%  {
            bottom: -3px;
            background-color: rgba(144, 174, 222, 0.5);
        }
        100% {
            bottom: 0px;
            background-color: rgba(113, 155, 224, 0.8);
        }
    }

