#LoginPage{
    position: absolute;
    display: block;
    width: 100%;
    max-width: 1500px;
    height: 100vh;
    top: 0;
    text-align: center;
    overflow: scroll;
    background: linear-gradient(
        to bottom right, 
        rgba(167, 194, 248, 0.95) 0%,
        rgba(169, 178, 196, 0.95) 10%,
        rgba(162, 180, 215, 0.95) 50%,
        rgba(162, 171, 190, 0.95) 90%,
        rgba(172, 197, 248, 0.95) 100%
    ); 
    box-shadow: inset 0px 0px 100px rgb(167, 194, 248);
    z-index: 10;

    -webkit-animation-name: fadeIn;

            animation-name: fadeIn;
    -webkit-animation-duration: .5s;
            animation-duration: .5s;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    transition-timing-function: ease-in-out;
}
    @-webkit-keyframes fadeIn {
        0% {
            opacity:0;
        }
        100%  {
            opacity:1;
        }
    }
    @keyframes fadeIn {
        0% {
            opacity:0;
        }
        100%  {
            opacity:1;
        }
    }

