.go-back-text{
    font-size: 22px;
    font-weight: bold;
    color: white;
    text-shadow: 0px 0px 8px rgba(0, 0, 0, 0.9);
}
.go-back-link{
    font-size: 20px;
    font-weight: bold;
    color: white;
    text-shadow: 0px 0px 8px rgba(73, 76, 95, 0.9);
    padding:8px 12px;
    background-color: rgba(76, 93, 144, 0.5);
    border-radius: 15px;
    cursor: pointer;

    -webkit-animation-name: goBackGlow;

            animation-name: goBackGlow;
    -webkit-animation-duration: 1.5s;
            animation-duration: 1.5s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out;
}
    @-webkit-keyframes goBackGlow {
    0%  {
        box-shadow: 0px 0px 3px rgb(255, 255, 255);
    }
    50%  {
        box-shadow: 0px 0px 15px rgb(255, 255, 255);
    }
    100% {
        box-shadow: 0px 0px 3px rgb(255, 255, 255);
    }
  }
    @keyframes goBackGlow {
    0%  {
        box-shadow: 0px 0px 3px rgb(255, 255, 255);
    }
    50%  {
        box-shadow: 0px 0px 15px rgb(255, 255, 255);
    }
    100% {
        box-shadow: 0px 0px 3px rgb(255, 255, 255);
    }
  }
.go-back-link:hover{
    -webkit-animation-name: none;
            animation-name: none;
    box-shadow: none;
}
.go-back-link:active{
    -webkit-animation-name: none;
            animation-name: none;
    box-shadow: inset 0px 0px 6px rgb(255, 255, 255);
}

