#Nav{
    position: relative;
    display: inline-block;
    text-align: center;
    width: 100%;
    height: fit-content;
    top: 60px;
    left: 0;
    /* box-shadow: 0px 0px 15px rgba(138, 169, 205, 0.3); */
}
#NavMenu{
    position: relative;
    display: inline-block;
    width: 100%;
    left: 0;
    text-shadow: 0px 1px 2px rgba(138, 169, 205, 0.5);
}
.navOptions{
    position: relative;
    display: inline-block;
    width: 120%;
    left: -11%;
    font-size: 24px;
    font-weight: 500;
    padding: 15px 0px 15px 0px;
    box-shadow: inset 0px 0px 15px rgba(132, 142, 154, 0.3);
    cursor: pointer;
}
.navOptions:hover{
    transform: scale(1.05);
    text-shadow: 0px 1px 2px rgba(132, 142, 154, 0.2);
    box-shadow: 0px 0px 15px rgb(132, 142, 154, 0.3);
}
.navOptions:active{
    text-shadow: none;
    box-shadow: inset 0px 0px 10px rgba(132, 142, 154, 0.2);
}
.text-grey{
    color: rgb(113, 120, 127);
    text-shadow: 0px 1px 2px rgba(73, 92, 115, 0.5);
}
.text-grey:hover{
    color: rgb(44, 44, 44);
    text-shadow: 0px 1px 2px rgba(73, 92, 115, 0.5);
}
.text-grey:active{
    color: rgb(55, 149, 203);
    /* text-shadow: 0px 0px 2px black; */
}
.controller{
    position: relative;
    display: inline-block;
    width: 120%;
    left: -11%;
    font-size: 16px;
    padding: 13px 0px 13px 0px;
    cursor: pointer;
}
.controller:hover{
    transform: scale(1.05);
    text-shadow: 0px 1px 2px rgba(138, 169, 205, 0.2);
    box-shadow: 0px 0px 15px rgb(138, 169, 205, 0.3);
}
.controller:active{
    text-shadow: none;
    box-shadow: inset 0px 0px 10px rgba(138, 169, 205, 0.2);
}
.social-header{
    margin-top: 30px;
    font-size: 22px;
    color: rgb(113, 120, 127);
    text-shadow: 0px 1px 2px rgba(73, 92, 115, 0.5);
}
#SocialMediaSection{
    position: flex;
    flex-direction: column;
    margin-top: -10px;
}
.linkedin{
    position: flex;
    flex-direction: row;
    display: inline-block;
    width: 65px;
    height: 65px;
    margin: 5px;
    background: url('../images/linkedin-icon.png');
    background-size: cover;
    background-repeat: no-repeat;
    cursor: pointer;
}
