.social-icon{
    width: 65px;
    height: 65px;
    border-radius: 15px;
    opacity: .8;
}
.social-icon:hover{
    transform: scale(1.02);
    opacity: .75;

    animation-name: shine;
    animation-duration: .3s;
    animation-iteration-count: 1;
    animation-timing-function: ease-in-out;
}
    @keyframes shine {
        0%  {
            opacity: .75;
        }
        20%  {
            opacity: .5;
        }
        100% {
            opacity: .75;
       }
    }

.icon:active{
    transform: scale(1);
    opacity: 1;
}




@media only screen and (max-width: 992px){
    .navButton{
        width: 100%;
        height: fit-content;
        top: 50px;
        left: 0;
    }
    #Nav{
        display: none;
    }
}